	module.exports = {"data":{"site":{"siteMetadata":{"startDate":"2018-06-06","lessons":8,"noClass":[4]}},"markdownRemark":{"html":"<h2>Goals of this homework</h2>\n<ol>\n<li>Use HTML tags from other homework</li>\n<li>Start to get comfortable with the format of CSS</li>\n<li>Use tag name selector</li>\n<li>Use class name selector</li>\n<li>Use id name selector</li>\n</ol>\n<p>In this homework you will be writing styles support the HTML that was provided. There is\nno need to make any changes to HTML.</p>\n<h2>Use HTML template</h2>\n<p>Copy the HTML form the text file provided and Paste in the body of your template.\nAdd Style tag with in the Head tag. (all styles will be added here)\nAdd the following style to the Style tag that was just added.</p>\n<hr>\n<h3>Tag Name Selectors</h3>\n<p>body</p>\n<pre class=\"text-content\">\n  font-family - arial\n  font-size - 16px\n  background-color - <span style=\"color: #3a7f7f;\">#3a7f7f</span>\n</pre>\n<p>h1</p>\n<pre class=\"text-content\">\n  color - <span style=\"color: #ffffff;;background-color:#000;padding:2px;\">#ffffff</span>\n  margin - 0\n</pre>\n<p>h2</p>\n<pre class=\"text-content\">\n  color - <span style=\"color: #3a7f7f;\">#3a7f7f</span>\n</pre>\n<p>h3</p>\n<pre class=\"text-content\">\n  color - <span style=\"color: #4b4b4b;\">#4b4b4b</span>\n</pre>\n<p>p</p>\n<pre class=\"text-content\">\n  line-height - 1.5em\n  color - <span style=\"color: #333333;\">#333333</span>\n</pre>\n<hr>\n<h3>Class Name Selector</h3>\n<p>.container</p>\n<pre class=\"text-content\">\n  width - 1000px\n</pre>\n<p>.header</p>\n<pre class=\"text-content\">\n  background-color - <span style=\"color: #4b4b4b;\">#4b4b4b</span>\n  padding - 1em\n</pre>\n<p>.navigation</p>\n<pre class=\"text-content\">\n  background-color - <span style=\"color: #ead967;\">#ead967</span>\n  padding - 1em\n</pre>\n<p>.navItem</p>\n<pre class=\"text-content\">\n  color - <span style=\"color: #4b4b4b;\">#4b4b4b</span>\n  text-decoration - none\n  font-weight - bold\n</pre>\n<p>.hero-image</p>\n<pre class=\"text-content\">\n  margin - 0\n</pre>\n<p>.content</p>\n<pre class=\"text-content\">\n  background-color - <span style=\"color: #e8e8e8;background-color:#000;padding:2px;\">#e8e8e8</span>\n  padding - 1em\n</pre>\n<p>.footer</p>\n<pre class=\"text-content\">\n  background-color - <span style=\"color: #ff9060;\">#ff9060</span>\n  padding - 1em\n  text-align - right\n</pre>","frontmatter":{"lesson":"Homework 3","title":"Lesson 3","lessonId":3,"downloads":{"Download_Stater_File":null,"Homework_Files":{"file":{"publicURL":"/static/homework3-text-3594e82e534cdc1ebfb1eb3733300156.txt"}}}}},"allMarkdownRemark":{"edges":[{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/3/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 3","lesson":"Introduction to CSS"},"fields":{"slug":"/3/index.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/3/selectors.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 3","lesson":"CSS Selectors"},"fields":{"slug":"/3/selectors.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/3/css.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 3","lesson":"CSS3 Reference"},"fields":{"slug":"/3/css.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/3/homework.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 3","lesson":"Homework 3"},"fields":{"slug":"/3/homework.html"}}}]}},"pathContext":{"slug":"/3/homework.html","title":"Lesson 3"}}

